<script>
import vueSeamlessScroll from "vue-seamless-scroll";
import $ from "jquery";
import Nav from "../components/Nav.vue";
import Footer from "../components/common/Footer.vue";
import RelationI from "../components/common/RelationI.vue";
import Service from "../components/common/Service.vue";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";
gsap.registerPlugin(ScrollTrigger);
import "../assets/js/jqRoll"
import { setCookie, getCookie } from "../assets/js/cookie"

export default {
  name: "Home",
  components: {
    Nav,
    Footer,
    RelationI,
    Service,
    vueSeamlessScroll
  },
  data() {
    return {
      // 品牌资讯数据
      message: {},
      endFlag: false,
    };
  },
  mounted() {
    this.scrollAnim();
    $(".first").roll({speed:6});
    $(".second").roll({speed:8});
    $(".thrid").roll({speed:4});
  },
  created() {
    this.getData();
  },
  methods: {
    scrollAnim() {
      if (!getCookie("view")) {
        setCookie("view", "1", 1);
        $(".l-loading").css({ opacity: 1 });
        $('.l-loading_layer1').addClass('is-animated');
        setTimeout(function() {
          $('.l-loading_layer2').addClass('is-animated');
        }, 2300);
        setTimeout(function() {
          $('.l-loading_layer3').addClass('is-animated');
          $('.l-loading_logo').addClass('is-animated');
        }, 3500);
        setTimeout(_ => {
          this.loading_hide();
          setTimeout(_ => {
            this.site_view();
          }, 300);
        }, 5800);
      } else {
        this.endFlag = true;
        this.loading_hide();
        this.site_view();
      }

      $("body").addClass("is-lock");
      $(".skip_btn").on("click", function () {
        this.loading_hide();
        this.site_view();
        this.endFlag = true;
      });
      var $background, $mask_size;
      if (window.innerWidth > 767) {
        $background = "circle(1200px at 50% 50%)";
        $mask_size = 2400;
      } else {
        $background = "circle(115vw at 50% 50%)";
        $mask_size = "230vw";
      }

      $("#index").css({ opacity: 1 });

      $(".count").each(function () {
        $(this)
          .prop("c", 2008)
          .animate(
            {
              c: 2022,
            },
            {
              duration: 2100,
              easing: "linear",
              step: function (now) {
                $(this).text(Math.ceil(now));
              },
            }
          );
      });
      $('.hover-in').hover(function () {
        $('.hover-in').removeClass('is-hover');
        $(this).addClass('is-hover');
        $('.section_bg').removeClass('is-active');
        $('.section_bg.' + $(this).data('bg')).addClass('is-active');
      }, function(){
        $(this).removeClass('is-hover');
        $('[data-bg="business_bg"]').addClass('is-hover');
        $('.section_bg').removeClass('is-active');
        $('.section_bg.business_bg').addClass('is-active');
      });
      ScrollTrigger.create({
        trigger: ".splash",
        start: "top top",
        endTrigger: ".splash",
        end: "bottom top",
        pin: true,
        pinSpacing: true,
        onUpdate: ({ progress, direction, isActive }) => {
          if (isActive) {
            $(".hero_mask").fadeIn();
          } else {
            $(".hero_mask").hide();
          }
        },
      });

      gsap.to("#background", {
        "clip-path": $background,
        ease: "power1.inOut",
        scrollTrigger: {
          trigger: "#background",
          start: "bottom bottom",
          end: "bottom top",
          scrub: true,
        },
      });

      gsap.to(".hero_mask", {
        width: $mask_size,
        rotate: 180,
        ease: "power1.inOut",
        scrollTrigger: {
          trigger: "#background",
          start: "bottom bottom",
          end: "bottom top",
          scrub: true,
        },
      });
    },
    loading_hide() {
      $(".l-loading").addClass("is-end");
      setTimeout(_ => {
        $(".l-loading").hide();
      }, 300);
    },
    site_view() {
      // $("#background").get(0).play();
      $(".hero_copyright").addClass("is-end");
      $(".hero_banner").addClass("is-end");
      $("body").removeClass("is-lock").addClass("is-end");
    },
    getData() {
      this.$axios
        .get("http://47.243.109.87:8082/caiweiGw/news/toList")
        .then((res) => {
          this.message = res.data.list;
          // console.log(res.data.list);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>
